            </div>
            
            <div class="time-details">
              <div class="detail-item">
                <span class="label">Âπ¥‰ªΩÔºö</span>
                <span class="value">{{ currentTime.year }}</span>
              </div>
              <div class="detail-item">
                <span class="label">Êúà‰ªΩÔºö</span>
                <span class="value">{{ currentTime.month }}</span>
              </div>
              <div class="detail-item">
