                <span class="value">{{ currentTime.time }}</span>
              </div>
            </div>
          </div>
        </el-card>

        <el-card class="timestamp-card" style="margin-top: 20px;">
          <div slot="header">
            <span>Êó∂Èó¥Êà≥‰ø°ÊÅØ</span>
          </div>
          
          <div class="timestamp-display">
            <div class="timestamp-item">
              <div class="timestamp-label">ÁßíÁ∫ßÊó∂Èó¥Êà≥</div>
              <div class="timestamp-value">
                {{ currentTime.timestamp }}
                <el-button size="mini" @click="copyToClipboard(currentTime.timestamp)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </div>
            </div>
            
            <div class="timestamp-item">
              <div class="timestamp-label">ÊØ´ÁßíÁ∫ßÊó∂Èó¥Êà≥</div>
              <div class="timestamp-value">
                {{ currentTime.timestampMs }}
                <el-button size="mini" @click="copyToClipboard(currentTime.timestampMs)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </div>
            </div>
          </div>
