          </div>
          
          <div class="format-list">
            <div v-for="format in timeFormats" :key="format.label" class="format-item">
              <div class="format-label">{{ format.label }}</div>
              <div class="format-value">
                {{ format.value }}
                <el-button size="mini" @click="copyToClipboard(format.value)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </div>
            </div>
          </div>
        </el-card>

        <el-card class="timezone-card" style="margin-top: 20px;">
          <div slot="header">
            <span>‰∏ñÁïåÊó∂Èó¥</span>
          </div>
          
          <div class="timezone-list">
            <div v-for="timezone in worldTimes" :key="timezone.name" class="timezone-item">
              <div class="timezone-name">{{ timezone.name }}</div>
              <div class="timezone-time">{{ timezone.time }}</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-card class="converter-card" style="margin-top: 20px;">
      <div slot="header">
        <span>Êó∂Èó¥Êà≥ËΩ¨Êç¢</span>
      </div>
      
      <el-row :gutter="20">
        <el-col :span="12">
          <div class="converter-section">
            <h4>Êó∂Èó¥Êà≥ËΩ¨Êó•Êúü</h4>
            <el-input
              v-model="inputTimestamp"
              placeholder="ËæìÂÖ•Êó∂Èó¥Êà≥ÔºàÁßíÊàñÊØ´ÁßíÔºâ"
              @input="convertTimestamp"
            >
              <el-button slot="append" @click="useCurrentTimestamp">ÂΩìÂâç</el-button>
            </el-input>
            <div class="convert-result" v-if="convertedTime">
              <p><strong>ËΩ¨Êç¢ÁªìÊûúÔºö</strong></p>
              <p>{{ convertedTime }}</p>
            </div>
          </div>
        </el-col>
        
        <el-col :span="12">
          <div class="converter-section">
            <h4>Êó•ÊúüËΩ¨Êó∂Èó¥Êà≥</h4>
            <el-date-picker
              v-model="inputDate"
              type="datetime"
              placeholder="ÈÄâÊã©Êó•ÊúüÊó∂Èó¥"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="convertDate"
              style="width: 100%"
            ></el-date-picker>
            <div class="convert-result" v-if="convertedTimestamp">
              <p><strong>ËΩ¨Êç¢ÁªìÊûúÔºö</strong></p>
              <p>ÁßíÁ∫ßÔºö{{ convertedTimestamp.seconds }}</p>
              <p>ÊØ´ÁßíÁ∫ßÔºö{{ convertedTimestamp.milliseconds }}</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DeviceUtil from '@/util/DeviceUtil'

export default {
  name: 'TimeViewerView',
  metaInfo: {
    title: 'Êó∂Èó¥Êü•ÁúãÂô® - ÂÆûÊó∂Âåó‰∫¨Êó∂Èó¥ÂíåÊó∂Èó¥Êà≥ËΩ¨Êç¢',
    meta: [
      { name: 'description', content: 'ÂÆûÊó∂ÊòæÁ§∫ÂΩìÂâçÂåó‰∫¨Êó∂Èó¥ÂíåÊó∂Èó¥Êà≥ÔºåÊîØÊåÅÂ§öÁßçÊó∂Èó¥Ê†ºÂºèÊòæÁ§∫ÂíåÊó∂Èó¥Êà≥ËΩ¨Êç¢ÂäüËÉΩ„ÄÇ' },
      { name: 'keywords', content: 'Âåó‰∫¨Êó∂Èó¥,Êó∂Èó¥Êà≥,Êó∂Èó¥ËΩ¨Êç¢,ÂÆûÊó∂Êó∂Èó¥,‰∏ñÁïåÊó∂Èó¥,Êó∂Èó¥Ê†ºÂºè' }
    ]
  },
  data() {
    return {
      currentTime: {
        format: '',
        year: '',
        month: '',
        day: '',
        weekday: '',
        time: '',
        timestamp: '',
        timestampMs: ''
      },
      timeFormats: [],
      worldTimes: [],
      autoUpdate: true,
      timer: null,
      inputTimestamp: '',
      convertedTime: '',
      inputDate: '',
      convertedTimestamp: null,
      isMobile: false,
      activeView: 'current'
    }
  },
  mounted() {
    this.updateTime()
    this.startTimer()
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    this.stopTimer()
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = DeviceUtil.isMobile()
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },

    updateTime() {
      const now = new Date()
      
      // ËΩ¨Êç¢‰∏∫Âåó‰∫¨Êó∂Èó¥ (UTC+8)
      const beijingTime = new Date(now.getTime() + (8 * 60 * 60 * 1000))
      
      this.currentTime = {
        format: this.formatDateTime(beijingTime),
        year: beijingTime.getFullYear(),
        month: beijingTime.getMonth() + 1,
        day: beijingTime.getDate(),
        weekday: this.getWeekday(beijingTime.getDay()),
        time: this.formatTime(beijingTime),
        timestamp: Math.floor(now.getTime() / 1000),
        timestampMs: now.getTime()
      }
      
      this.updateTimeFormats(beijingTime)
      this.updateWorldTimes(now)
    },

    formatDateTime(date) {
      return date.getFullYear() + '-' +
             String(date.getMonth() + 1).padStart(2, '0') + '-' +
             String(date.getDate()).padStart(2, '0') + ' ' +
             String(date.getHours()).padStart(2, '0') + ':' +
             String(date.getMinutes()).padStart(2, '0') + ':' +
             String(date.getSeconds()).padStart(2, '0')
    },

    formatTime(date) {
      return String(date.getHours()).padStart(2, '0') + ':' +
             String(date.getMinutes()).padStart(2, '0') + ':' +
             String(date.getSeconds()).padStart(2, '0')
    },

    getWeekday(day) {
      const weekdays = ['ÊòüÊúüÊó•', 'ÊòüÊúü‰∏Ä', 'ÊòüÊúü‰∫å', 'ÊòüÊúü‰∏â', 'ÊòüÊúüÂõõ', 'ÊòüÊúü‰∫î', 'ÊòüÊúüÂÖ≠']
      return weekdays[day]
    },

    updateTimeFormats(beijingTime) {
      this.timeFormats = [
        {
          label: 'ISO 8601',
          value: beijingTime.toISOString().replace('Z', '+08:00')
        },
        {
          label: 'RFC 2822',
          value: beijingTime.toString()
        },
        {
          label: 'UTCÊó∂Èó¥',
          value: new Date().toUTCString()
        },
        {
          label: '‰∏≠ÊñáÊ†ºÂºè',
          value: `${beijingTime.getFullYear()}Âπ¥${beijingTime.getMonth() + 1}Êúà${beijingTime.getDate()}Êó• ${this.formatTime(beijingTime)}`
        },
        {
          label: 'ÁæéÂºèÊ†ºÂºè',
          value: `${beijingTime.getMonth() + 1}/${beijingTime.getDate()}/${beijingTime.getFullYear()} ${this.formatTime(beijingTime)}`
        },
        {
          label: 'Ê¨ßÂºèÊ†ºÂºè',
          value: `${beijingTime.getDate()}/${beijingTime.getMonth() + 1}/${beijingTime.getFullYear()} ${this.formatTime(beijingTime)}`
        }
      ]
    },

    updateWorldTimes(now) {
      const timezones = [
        { name: 'Á∫ΩÁ∫¶ (UTC-5)', offset: -5 },
        { name: '‰º¶Êï¶ (UTC+0)', offset: 0 },
        { name: 'Â∑¥Èªé (UTC+1)', offset: 1 },
        { name: 'Ëé´ÊñØÁßë (UTC+3)', offset: 3 },
        { name: 'Ëø™Êãú (UTC+4)', offset: 4 },
        { name: '‰∏ú‰∫¨ (UTC+9)', offset: 9 },
        { name: 'ÊÇâÂ∞º (UTC+10)', offset: 10 }
      ]

      this.worldTimes = timezones.map(tz => {
        const time = new Date(now.getTime() + (tz.offset * 60 * 60 * 1000))
        return {
          name: tz.name,
          time: this.formatDateTime(time)
        }
      })
    },

    startTimer() {
      this.timer = setInterval(() => {
        if (this.autoUpdate) {
          this.updateTime()
        }
      }, 1000)
    },

    stopTimer() {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },

    toggleAutoUpdate() {
      this.autoUpdate = !this.autoUpdate
      if (this.autoUpdate) {
        this.updateTime()
      }
    },

    convertTimestamp() {
      if (!this.inputTimestamp) {
        this.convertedTime = ''
        return
      }

      try {
        let timestamp = parseInt(this.inputTimestamp)
        
        // Âà§Êñ≠ÊòØÁßíÁ∫ßËøòÊòØÊØ´ÁßíÁ∫ßÊó∂Èó¥Êà≥
        if (timestamp < 10000000000) {
          // ÁßíÁ∫ßÊó∂Èó¥Êà≥
          timestamp = timestamp * 1000
        }
        
        const date = new Date(timestamp)
        const beijingTime = new Date(date.getTime() + (8 * 60 * 60 * 1000))
        
        if (isNaN(date.getTime())) {
          this.convertedTime = 'Êó†ÊïàÁöÑÊó∂Èó¥Êà≥'
        } else {
          this.convertedTime = this.formatDateTime(beijingTime) + ' (Âåó‰∫¨Êó∂Èó¥)'
        }
      } catch (error) {
        this.convertedTime = 'ËΩ¨Êç¢Â§±Ë¥•'
      }
    },

    convertDate() {
      if (!this.inputDate) {
        this.convertedTimestamp = null
        return
      }

      try {
        const date = new Date(this.inputDate)
        // ÂáèÂéª8Â∞èÊó∂ËΩ¨Êç¢‰∏∫UTCÊó∂Èó¥
        const utcDate = new Date(date.getTime() - (8 * 60 * 60 * 1000))
        
        this.convertedTimestamp = {
          seconds: Math.floor(utcDate.getTime() / 1000),
          milliseconds: utcDate.getTime()
        }
      } catch (error) {
        this.convertedTimestamp = null
      }
    },

    useCurrentTimestamp() {
      this.inputTimestamp = this.currentTime.timestamp.toString()
      this.convertTimestamp()
    },

    async copyToClipboard(text) {
      try {
        const success = await ClipboardUtil.copyText(text.toString())
        if (success) {
          this.$message.success('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•: ' + error.message)
      }
    }
  }
}
</script>

